"use client"

import * as React from "react"
import {
  getCoreRowModel,
  getFilteredRowModel,
  getPaginationRowModel,
  getSortedRowModel,
  useReactTable,
} from "@tanstack/react-table"
import { IconDatabase } from "@tabler/icons-react"

import { Table, TableBody, TableContainer } from "@/components/ui/table"

import { timeFormatter } from "./constants"
import { createColumnDefs } from "./column-defs"
import { createGlobalFilterFn } from "./global-filter"
import { useDataTableState } from "./use-data-table"
import { DataTableHeader } from "./components/table-header"
import { QuickFiltersPanel } from "./components/quick-filters-panel"
import { TableBodyContent } from "./components/table-body-content"
import { TableFooter } from "./components/table-footer"
import { QUICK_FILTER_DEFINITIONS, MULTI_KEYS, createInitialQuickFilters } from "./quick-filters"

export function DataTable({ lineId }) {
  const {
    columns,
    rows,
    filter,
    setFilter,
    sorting,
    setSorting,
    isLoadingRows,
    rowsError,
    tableMeta,
  } = useDataTableState({ lineId })

  const firstRow = rows[0]
  const columnDefs = React.useMemo(
    () => createColumnDefs(columns, undefined, firstRow),
    [columns, firstRow]
  )
  const globalFilterFn = React.useMemo(() => createGlobalFilterFn(columns), [columns])

  const [pagination, setPagination] = React.useState({ pageIndex: 0, pageSize: 15 })
  const [quickFilters, setQuickFilters] = React.useState(() => createInitialQuickFilters())
  const [columnSizing, setColumnSizing] = React.useState({})

  const quickFilterSections = React.useMemo(() => {
    return QUICK_FILTER_DEFINITIONS.map((definition) => {
      const columnKey = definition.resolveColumn(columns)
      if (!columnKey) return null

      const valueMap = new Map()
      rows.forEach((row) => {
        const rawValue = row?.[columnKey]
        const normalized = definition.normalizeValue(rawValue)
        if (normalized === null) return
        if (!valueMap.has(normalized)) {
          valueMap.set(normalized, definition.formatValue(normalized, rawValue))
        }
      })
      if (valueMap.size === 0) return null

      const options = Array.from(valueMap.entries()).map(([value, label]) => ({ value, label }))
      if (typeof definition.compareOptions === "function") {
        options.sort((a, b) => definition.compareOptions(a, b))
      }

      return {
        key: definition.key,
        label: definition.label,
        options,
        getValue: (row) => definition.normalizeValue(row?.[columnKey]),
        isMulti: MULTI_KEYS.has(definition.key),
      }
    }).filter(Boolean)
  }, [columns, rows])

  React.useEffect(() => {
    const sectionMap = new Map(quickFilterSections.map((section) => [section.key, section]))
    setQuickFilters((prev) => {
      let next = prev

      for (const definition of QUICK_FILTER_DEFINITIONS) {
        const section = sectionMap.get(definition.key)
        const currentValue = prev[definition.key]

        if (!section) {
          const resetValue = MULTI_KEYS.has(definition.key) ? [] : null
          if (JSON.stringify(currentValue) !== JSON.stringify(resetValue)) {
            if (next === prev) next = { ...prev }
            next[definition.key] = resetValue
          }
          continue
        }

        const validValues = new Set(section.options.map((option) => option.value))
        if (section.isMulti) {
          const currentArray = Array.isArray(currentValue) ? currentValue : []
          const filtered = currentArray.filter((value) => validValues.has(value))
          if (currentArray.length !== filtered.length) {
            if (next === prev) next = { ...prev }
            next[definition.key] = filtered
          }
        } else if (currentValue !== null && !validValues.has(currentValue)) {
          if (next === prev) next = { ...prev }
          next[definition.key] = null
        }
      }

      return next
    })
  }, [quickFilterSections])

  const filteredRows = React.useMemo(() => {
    if (quickFilterSections.length === 0) return rows

    return rows.filter((row) =>
      quickFilterSections.every((section) => {
        const current = quickFilters[section.key]
        const rowValue = section.getValue(row)

        if (section.isMulti) {
          return Array.isArray(current) && current.length > 0 ? current.includes(rowValue) : true
        }

        return current !== null ? rowValue === current : true
      })
    )
  }, [rows, quickFilterSections, quickFilters])

  /* eslint-disable react-hooks/incompatible-library */
  const table = useReactTable({
    data: filteredRows,
    columns: columnDefs,
    meta: tableMeta,
    state: {
      sorting,
      globalFilter: filter,
      pagination,
      columnSizing,
    },
    onSortingChange: setSorting,
    onGlobalFilterChange: setFilter,
    onPaginationChange: setPagination,
    onColumnSizingChange: setColumnSizing,
    globalFilterFn,
    getCoreRowModel: getCoreRowModel(),
    getFilteredRowModel: getFilteredRowModel(),
    getSortedRowModel: getSortedRowModel(),
    getPaginationRowModel: getPaginationRowModel(),
    columnResizeMode: "onChange",
  })
  /* eslint-enable react-hooks/incompatible-library */

  const emptyStateColSpan = Math.max(table.getVisibleLeafColumns().length, 1)
  const totalLoaded = rows.length
  const filteredTotal = filteredRows.length
  const hasNoRows = !isLoadingRows && rowsError === null && columns.length === 0
  const [lastUpdatedLabel, setLastUpdatedLabel] = React.useState(null)
  const currentPage = pagination.pageIndex + 1
  const totalPages = Math.max(table.getPageCount(), 1)
  const currentPageSize = table.getRowModel().rows.length

  React.useEffect(() => {
    if (isLoadingRows) return
    setLastUpdatedLabel(timeFormatter.format(new Date()))
  }, [isLoadingRows])

  React.useEffect(() => {
    setPagination((prev) => (prev.pageIndex === 0 ? prev : { ...prev, pageIndex: 0 }))
  }, [filter, sorting, quickFilters])

  React.useEffect(() => {
    const maxIndex = Math.max(table.getPageCount() - 1, 0)
    setPagination((prev) =>
      prev.pageIndex > maxIndex ? { ...prev, pageIndex: maxIndex } : prev
    )
  }, [table, rows.length, filteredRows.length, pagination.pageSize])

  const activeQuickFilterCount = React.useMemo(
    () =>
      Object.entries(quickFilters).reduce((sum, [key, value]) => {
        if (MULTI_KEYS.has(key)) {
          return sum + (Array.isArray(value) ? value.length : 0)
        }
        return sum + (value !== null ? 1 : 0)
      }, 0),
    [quickFilters]
  )

  const handleQuickFilterToggle = React.useCallback((key, value) => {
    setQuickFilters((prev) => {
      const isMulti = MULTI_KEYS.has(key)

      if (value === null) {
        return { ...prev, [key]: isMulti ? [] : null }
      }

      if (!isMulti) {
        return { ...prev, [key]: prev[key] === value ? null : value }
      }

      const currentArray = Array.isArray(prev[key]) ? prev[key] : []
      const exists = currentArray.includes(value)
      const nextArray = exists
        ? currentArray.filter((candidate) => candidate !== value)
        : [...currentArray, value]

      return { ...prev, [key]: nextArray }
    })
  }, [])

  const handleQuickFilterClearAll = React.useCallback(
    () => setQuickFilters(() => createInitialQuickFilters()),
    []
  )

  return (
    <section className="flex h-full min-h-0 min-w-0 flex-col gap-2 px-4 lg:px-6">
      <div className="flex flex-wrap items-start justify-between gap-4">
        <div className="flex flex-col gap-1">
          <div className="flex items-center gap-2 text-lg font-semibold">
            <IconDatabase className="size-5" />
            {lineId}Line E-SOP Status
          </div>
        </div>
      </div>

      <QuickFiltersPanel
        sections={quickFilterSections}
        quickFilters={quickFilters}
        onToggle={handleQuickFilterToggle}
        onClearAll={handleQuickFilterClearAll}
        activeCount={activeQuickFilterCount}
      />

      <TableContainer className="flex-1 h-[calc(100vh-3rem)] overflow-y-auto overflow-x-auto rounded-lg border px-1">
        <Table className="table-fixed w-full" style={{ width: table.getTotalSize() }} stickyHeader>
          <colgroup>
            {table.getVisibleLeafColumns().map((column) => (
              <col key={column.id} style={{ width: column.getSize() }} />
            ))}
          </colgroup>

          <DataTableHeader table={table} />
          <TableBody>
            <TableBodyContent
              table={table}
              emptyStateColSpan={emptyStateColSpan}
              isLoadingRows={isLoadingRows}
              rowsError={rowsError}
              hasNoRows={hasNoRows}
            />
          </TableBody>
        </Table>
      </TableContainer>

      <TableFooter
        table={table}
        pagination={pagination}
        currentPage={currentPage}
        totalPages={totalPages}
        currentPageSize={currentPageSize}
        filteredTotal={filteredTotal}
        totalLoaded={totalLoaded}
        isLoadingRows={isLoadingRows}
        lastUpdatedLabel={lastUpdatedLabel}
      />
    </section>
  )
}
