import {
  IconChevronLeft,
  IconChevronRight,
  IconChevronsLeft,
  IconChevronsRight,
} from "@tabler/icons-react"

import { Button } from "@/components/ui/button"

import { numberFormatter } from "../constants"

function TableFooter({
  table,
  pagination,
  currentPage,
  totalPages,
  currentPageSize,
  filteredTotal,
  totalLoaded,
  isLoadingRows,
  lastUpdatedLabel,
}) {
  const updatedLabel = isLoadingRows ? "just now" : lastUpdatedLabel ?? "just now"

  return (
    <div className="flex flex-col gap-2 lg:flex-row lg:items-center lg:justify-between">
      <div className="flex flex-wrap items-center gap-2 text-xs text-muted-foreground">
        <span>
          Showing {numberFormatter.format(currentPageSize)} of {numberFormatter.format(filteredTotal)} rows
          {filteredTotal !== totalLoaded ? ` (filtered from ${numberFormatter.format(totalLoaded)})` : ""}
        </span>
        <span>(Updated {updatedLabel})</span>
      </div>

      <div className="flex flex-col gap-2 sm:flex-row sm:items-center sm:justify-end">
        <div className="flex items-center gap-1">
          <Button
            variant="outline"
            size="sm"
            onClick={() => table.setPageIndex(0)}
            disabled={!table.getCanPreviousPage()}
            aria-label="Go to first page"
          >
            <IconChevronsLeft className="size-4" />
          </Button>
          <Button
            variant="outline"
            size="sm"
            onClick={() => table.previousPage()}
            disabled={!table.getCanPreviousPage()}
            aria-label="Go to previous page"
          >
            <IconChevronLeft className="size-4" />
          </Button>
          <span className="px-2 text-sm font-medium">
            Page {numberFormatter.format(currentPage)} of {numberFormatter.format(totalPages)}
          </span>
          <Button
            variant="outline"
            size="sm"
            onClick={() => table.nextPage()}
            disabled={!table.getCanNextPage()}
            aria-label="Go to next page"
          >
            <IconChevronRight className="size-4" />
          </Button>
          <Button
            variant="outline"
            size="sm"
            onClick={() => table.setPageIndex(totalPages - 1)}
            disabled={!table.getCanNextPage()}
            aria-label="Go to last page"
          >
            <IconChevronsRight className="size-4" />
          </Button>
        </div>

        <label className="flex items-center gap-2 text-sm">
          <span className="text-xs text-muted-foreground">Rows per page</span>
          <select
            value={pagination.pageSize}
            onChange={(event) => table.setPageSize(Number(event.target.value))}
            className="h-8 rounded-md border border-input bg-background px-2 text-sm text-foreground shadow-sm focus:outline-none focus:ring-2 focus:ring-ring/50"
          >
            {[15, 25, 30, 40, 50].map((size) => (
              <option key={size} value={size}>
                {size}
              </option>
            ))}
          </select>
        </label>
      </div>
    </div>
  )
}

export { TableFooter }
