import { flexRender } from "@tanstack/react-table"

import { cn } from "@/lib/utils"
import { TableCell, TableRow } from "@/components/ui/table"

import { getTextAlignClass, resolveCellAlignment } from "../alignment"

function isNullishDisplay(value) {
  if (value == null) return true
  if (typeof value === "string" && value.trim().toLowerCase() === "null") return true
  return false
}

function renderEmptyRow(colSpan, message, className) {
  return (
    <TableRow>
      <TableCell colSpan={colSpan} className={className}>
        {message}
      </TableCell>
    </TableRow>
  )
}

function TableBodyContent({ table, emptyStateColSpan, isLoadingRows, rowsError, hasNoRows }) {
  if (isLoadingRows) {
    return renderEmptyRow(
      emptyStateColSpan,
      "Loading rows‚Ä¶",
      "h-26 text-center text-sm text-muted-foreground"
    )
  }

  if (rowsError) {
    return renderEmptyRow(
      emptyStateColSpan,
      rowsError,
      "h-26 text-center text-sm text-destructive"
    )
  }

  if (hasNoRows) {
    return renderEmptyRow(
      emptyStateColSpan,
      "No rows returned.",
      "h-26 text-center text-sm text-muted-foreground"
    )
  }

  const visibleRows = table.getRowModel().rows
  if (visibleRows.length === 0) {
    return renderEmptyRow(
      emptyStateColSpan,
      "No rows match your filter.",
      "h-26 text-center text-sm text-muted-foreground"
    )
  }

  return visibleRows.map((row) => (
    <TableRow key={row.id}>
      {row.getVisibleCells().map((cell) => {
        const isEditable = Boolean(cell.column.columnDef.meta?.isEditable)
        const cellAlignment = resolveCellAlignment(cell.column.columnDef.meta)
        const cellTextClass = getTextAlignClass(cellAlignment)
        const width = cell.column.getSize()
        const rawValue = cell.getValue()
        const content = isNullishDisplay(rawValue)
          ? ""
          : flexRender(cell.column.columnDef.cell, cell.getContext())

        return (
          <TableCell
            key={cell.id}
            data-editable={isEditable ? "true" : "false"}
            style={{ width, minWidth: width, maxWidth: width }}
            className={cn(
              "align-center",
              cellTextClass,
              !isEditable && "caret-transparent focus:outline-none"
            )}
          >
            {content}
          </TableCell>
        )
      })}
    </TableRow>
  ))
}

export { TableBodyContent }
